.postPage {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: .5rem;
}

.errorMessage {
  color: red;
  text-align: center;
  margin-top: 2rem;
}

.loading {
  text-align: center;
  margin-top: 2rem;
}

.postImage {
  width: 100%;
  object-fit: cover;
  display: flex;
  justify-content: center;
}

.postCover {
  width: 100%;
  height: auto;
}

.postTitle {
  text-align: center;
  font-size: 2.5rem;
  margin-top: 2rem;
}

.postAuthor {
  text-align: center;
  margin-top: 1rem;
  font-style: italic;
  color: #777;
}

.icons {
  display: flex;
  justify-content: flex-end;
  margin-top: -2rem;
}

.editIcon {
  width: 25px;
  height: 25px;
  cursor: pointer;
  color: rgb(0, 123, 255);
}

.editIcon:hover {
  color: rgb(0, 0, 255);
}

.deleteIcon {
  width: 25px;
  height: 25px;
  cursor: pointer;
  color: rgb(252, 143, 160);
}

.deleteIcon:hover {
  color: rgb(255, 0, 0);
}

.postContent {
  margin-top: 1rem;
  font-size: 1.2rem;
  line-height: 1.5;
  word-wrap: break-word;
}

.tagsContainer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: .5rem;
  margin-top: 1rem;
  border-top: 1px solid #ddd;
  padding-top: .5rem;
}

.tag {
  background-color: #d0e9e5;
  padding: .5rem;
  border-radius: .5rem;
  font-size: .7rem;
  color: #333;
}

.tag:hover {
  background-color: #c0e0dc;
  cursor: pointer;
}

.commentOrRate {
  background-color: #ececec;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  padding: 1rem;
  margin-top: 1rem;
  border-radius: .5rem;
  height: 220px;
}

.createCommentContainer {
  width: 100%;
}

.postRatingContainer {
  background-color: #f9f9f9;
  border: 1px solid #d0e9e5;
  font-size: .85rem;
  width: 12rem;
  height: 9rem;
  padding: .5rem .25rem;
  border-radius: .5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .5rem;
}

.postAverageRating {

}

.postRating {
  padding-top: .5rem;
  text-align: center;
  border-top: 1px solid #ccc;
}

.postRating button {
  height: 1.5rem;
  width: 80%;
  font-size: .7rem;
  border-radius: .25rem;
  border: none;
  cursor: pointer;
  border: 1px solid #d0e9e5;
}

.postRating button:hover {
  background-color: #d0e9e5;
}

.postRatingStars {
  display: flex;
  margin-top: .25rem;
  padding-bottom: .5rem;
}

.postRatingStars svg {
  width: 28px;
  height: 28px;
  cursor: pointer;
}
